body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*Не забывать побить стили*/
html, body, #root {
  min-height: 100vh;
  min-width: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.menu {
  width: 200px;
}

.menu__header {
  font-size: 20px;
  font-weight: 700;
}

.card {
  max-width: 400px;
  margin: 10px;
  border-radius: 25px;
  overflow: hidden;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.card__image {
  height: 200px;
}

.no-style {
  text-decoration: none;
  width: 100%;
  color: inherit;
}

.page {
  color: rgba(0, 0, 0, .7);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  background-color: #eeeeee;
}

.content {
  margin-top: 60px;
  -webkit-flex: 1 0 auto;
          flex: 1 0 auto;
}

.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  color: #333;
}

.wrapper {
  margin-top: 7px;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}


.footer__center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.footer__text {
  display: inline-block;
}

.footer__mail {
  color: rgba(0, 0, 0, .7);
  padding-left: 5px;

}

.event {
  margin: 0 10px;
  margin-bottom: 10px;
}

.event__header {
  margin: 30px 0;
  text-align: center;
}

.event__images {
  height: 60vw;
  display: block;
  overflow: hidden;
  width: 100%;
}

.event__text {
  margin: 20px 40px;
}

.event__address {
  text-align: right;
  margin-bottom: 50px;
  margin-right: 40px;
  font-weight: 700;
}

.back {
  margin-top: 7px;
}

.event__control {
  display: none;
}

.header__checkbox {
  background-color: #fff;
}

@media (min-width: 1280px) {
  .content {
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    margin: 60px auto 0 auto;
    width: 1280px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  .event__images_width {
    width: 1280px;
  }

  .event__images {
    height: 700px;
    display: block;
    overflow: hidden;
    width: 100%;
  }

  .center {
    width: 1280px;
    margin: 0 auto;
  }


}

@media (min-width: 500px) {
  .event__control {
    display: inline;
  }
}

